.mfa-register-totp__code.form-control {
  font-size: 2rem;
  letter-spacing: 1rem;
  width: 80%;
}

.mfa-register-totp__validate-code {
  display: flex;
}

.mfa-register-totp__validate-left {
  flex: 1;
}

.mfa-register-totp__validate-left p {
  margin-bottom: 3rem;
}

.mfa-register-totp__validate-img {
  height: 200px;
}

.mfa-register-totp__validate-right {
  margin-left: 1rem;
  margin-top: -2rem;
  text-align: center;
}

.mfa-register-totp__scan-code {
  align-items: stretch;
  display: flex;
  margin: 3rem 0;
}

.mfa-register-totp__scan-left {
  flex: 1;
  height: 160px;
}

.mfa-register-totp__scan-middle {
  align-items: center;
  display: flex;
  flex: 1;
  justify-content: center;
  text-transform: uppercase;
}

.mfa-register-totp__scan-right {
  border: 1px solid #cccccc;
  display: flex;
  flex: 2;
  flex-direction: column;
  justify-content: center;
  padding: 1rem;
  text-align: center;
}

.mfa-register-totp__scan-right p:last-child {
  margin: 0;
}

@media (max-width: 480px) {
  .mfa-register-totp__scan-code {
    align-items: center;
    flex-direction: column;
  }

  .mfa-register-totp__scan-left,
  .mfa-register-totp__scan-middle {
    margin-bottom: 1rem;
  }
}




